/* box-sizing  . */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/*    . */
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

/*     CSS     . */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin-block-end: 0;
  margin: 0;
  padding: 0;
}

/* list role  ul, ol     . */
ul[role="list"],
ol[role="list"] {
  list-style: none;
}

/*  body  . */
body {
  min-height: 100vh;
  line-height: 1.5;
}

/*      line-height   . */
h1,
h2,
h3,
h4,
button,
input,
label {
  line-height: 1.1;
}

/*   text-wrap balance . */
h1,
h2,
h3,
h4 {
  text-wrap: balance;
}

/*    a     . */
a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

/*      . */
img,
picture {
  max-width: 100%;
  display: block;
}

/* input  button    . */
input,
button,
textarea,
select {
  font: inherit;
}

/*    textarea    . */
textarea:not([rows]) {
  min-height: 10em;
}

/*        . */
:target {
  scroll-margin-block: 5ex;
}

#inner_wrap {
  margin: 0 auto;
}

ul,
li {
  list-style: none;
}

a {
  color: #333;
  text-decoration: none;
}


main {
  padding-bottom: 40px;
}